/* src/components/StatisticsSection.css */
.statistics-section {
  min-height: 100vh;
  padding: 40px 20px;
  background-color: #fff;
  text-align: center;
  border-top: 1px solid #ddd;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.statistics-section h2 {
  font-size: 2.2em;
  color: #333;
}

.stats-container {
  display: flex;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
  margin-top: 20px;
}

.stat-item {
  background: #f9f9f9;
  padding: 20px;
  border-radius: 8px;
  text-align: center;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.stat-value {
  font-size: 2em;
  color: #007bff;
  font-weight: bold;
}

.stat-label {
  margin-top: 10px;
  font-size: 1em;
  color: #555;
}
