/* src/components/News.css */
.news-section {
  min-height: 100vh;
  padding: 40px 20px;
  background-color: #f9f9f9;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.news-section h2 {
  font-size: 2.2em;
  color: #333;
  margin-bottom: 20px;
}

.news-container {
  display: flex;
  gap: 20px;
  overflow-x: auto;
  padding: 10px;
  scroll-behavior: smooth; /* 부드러운 스크롤 */
}

.news-container::-webkit-scrollbar {
  height: 15px; /* 스크롤바 높이 */
}

.news-container::-webkit-scrollbar-thumb {
  background-color: #aaa; /* 스크롤바 색상 */
  border-radius: 2vh;
}

.news-container::-webkit-scrollbar-track {
  background-color: #f0f0f0; /* 스크롤바 트랙 색상 */
}

.news-box {
  flex: 0 0 40vh; /* 각 박스 크기 고정 */
  height: 40vh; /* 정사각형 박스 */
  background-color: #ffffff;
  border: 1px solid #ddd;
  border-radius: 10px;
  padding: 2vh;
  box-shadow: 0 2vh 4vh rgba(0, 0, 0, 0.1);
  text-align: center;
  transition: transform 0.2s;
}

.news-box:hover {
  transform: scale(1.05); /* 호버 시 확대 효과 */
}

.news-box h3 {
  font-size: 1.5em;
  margin-bottom: 10px;
  color: #444;
}

.news-box p {
  font-size: 1em;
  color: #666;
}
