/* 기본 설정 */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Arial", sans-serif;
  background-color: #f4f4f9; /* 부드러운 배경 색 */
  color: #333;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

h1,
h2,
h3 {
  font-weight: 700;
  color: #315eaa; /* 어두운 색으로 제목 강조 */
}

/* 헤더 스타일 */
header {
  background-color: #3498db; /* 파란색 배경 */
  color: white;
  padding: 20px 0;
  text-align: center;
  font-size: 4rem;
  font-weight: bold;
}

header nav {
  display: flex;
  justify-content: center;
  gap: 20px;
}

header nav a {
  color: white;
  text-decoration: none;
  font-size: 1.2rem;
}

header nav a:hover {
  text-decoration: underline;
}

/* 메인 콘텐츠 스타일 */
.main-content {
  flex: 1;
  padding: 40px 20px;
  background-color: white;
  text-align: center;
}

.main-content h2 {
  margin-bottom: 20px;
}

.main-content p {
  font-size: 1.1rem;
  line-height: 1.6;
  color: #555;
}

.main-content .cta-button {
  background-color: #2ecc71; /* 클릭 유도 버튼 */
  color: white;
  padding: 15px 30px;
  border: none;
  border-radius: 5px;
  font-size: 1.2rem;
  cursor: pointer;
  transition: background-color 0.3s;
}

.main-content .cta-button:hover {
  background-color: #27ae60;
}

/* Features 섹션 */
.features-container {
  /* cursor: pointer; */
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  align-items: center;
  position: relative;
  padding: 20px;
  height: 100vh;
  overflow: hidden;
  background-color: #ffffff;
}

.left-image,
.right-image {
  object-fit: cover;
  width: 100%;
  height: 100vh;
}

.right-image {
  grid-row: 1;
  grid-column: 3;
  transform: translateY(-50%);
  mask-image: linear-gradient(to top, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
  -webkit-mask-image: linear-gradient(
    to top,
    rgba(0, 0, 0, 1),
    rgba(0, 0, 0, 0)
  );
}

.text {
  grid-row: 1;
  grid-column: 2;
  position: relative;
  display: inline-block;
}

ul {
  display: inline-block;
  list-style-type: none;
}
ul li {
  margin-bottom: 20px;
}

.description {
  font-size: 0.9rem; /* 설명 텍스트 크기 */
  color: #7f8c8d; /* 설명 텍스트 색상 */
  margin-top: 5px;
}

.left-image {
  grid-row: 1;
  grid-column: 1;
  transform: translateY(50%);
  mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
  -webkit-mask-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 1),
    rgba(0, 0, 0, 0)
  );
}

.falling-image img {
  width: 10vw;
  height: 10vw;
  position: absolute;
  top: -10vh;
  transform: translateX(-50%);
  animation: fall 1s ease-in-out forwards;
  z-index: 10;
}

@keyframes fall {
  0% {
    top: -20vh;
    opacity: 0.9;
  }
  100% {
    top: 120vh; /* 이미지가 떨어지는 끝 위치 */
    opacity: 0;
  }
}

/* Footer 기본 스타일 */
footer {
  display: flex;
  justify-content: space-between;
  background-color: #333;
  padding: 40px 20px;
  font-family: Arial, sans-serif;
  color: #f4f4f9;
}

/* 섹션들 */
.footer-section {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

/* 상단 섹션 */
.footer-section .company-info {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.footer-section .company-info p {
  margin: 5px 0;
}

.footer-section .terms-links {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.footer-section .terms-links a {
  margin: 5px 0;
  text-decoration: none;
  color: #f4f4f9;
}

.footer-section .terms-links a:hover {
  text-decoration: none;
}

/* 중간 섹션 */
.footer-section .social-media {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.footer-section .social-media a {
  margin: 5px 0;
  text-decoration: none;
  color: #f4f4f9;
}

.footer-section .social-logo {
  width: 2.5vh;
  height: 2.5vh;
}

.footer-section .social-media a:hover {
  text-decoration: none;
}

/* 언어 옵션 */
.footer-section .language-options {
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
}

.footer-section .language-options button {
  margin: 5px 0;
  padding: 5px 15px;
  border: none;
  background-color: #007bff;
  color: white;
  cursor: pointer;
}

.footer-section .language-options button:hover {
  background-color: #0056b3;
}

/* 하단 섹션 */
.footer-section .company-logo {
  flex: 1;
  display: flex;
  justify-content: flex-start;
}

.footer-section .company-logo img {
  max-width: 150px;
  height: auto;
}

.footer-section .credits {
  flex: 1;
  display: flex;
  justify-content: flex-end;
}

.footer-section .credits p {
  font-size: 14px;
}

/* 언어 옵션 버튼 스타일 */
.language-options {
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
}

.language-btn {
  margin: 5px 0;
  padding: 5px 15px;
  border: none;
  background-color: #007bff;
  color: white;
  cursor: pointer;
  font-size: 1rem; /* 글자 크기 일정 */
  transition: background-color 0.3s, color 0.3s;
  white-space: nowrap; /* 텍스트 줄바꿈 방지 */
}

/* 영어 버튼 텍스트를 변경 */
.language-btn#english {
  transition: color 0.3s;
}

.language-btn#english:hover {
  color: transparent; /* 원본 텍스트 숨기기 */
}

/* 버튼에 커서를 올렸을 때 텍스트 바꾸기 */
.language-btn#english:hover::after {
  content: "영어"; /* 텍스트 바꾸기 */
  color: white; /* 글자 색 */
}

/* 반응형 디자인 */
@media (max-width: 767px) {
  header {
    font-size: 1.8rem;
  }

  .main-content {
    padding: 30px 15px;
  }

  .main-content h2 {
    font-size: 1.5rem;
  }

  footer {
    font-size: 0.8rem;
  }
}
