/* 기본 스타일 */
.App {
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow-y: auto; /* 전체 페이지에 스크롤을 추가 */
}

.App-header, .ai-features, .interactive-demo {
  min-height: 100vh; /* 각 섹션을 화면의 높이에 맞게 설정 */
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.App-header {
  background-color: #282c34;
  color: white;
}

.ai-features {
  background-color: #f0f0f0;
  text-align: center;
}

.interactive-demo {
  background-color: #4caf50;
  color: white;
}

button {
  background-color: #61dafb;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
}

button:hover {
  background-color: #21a1f1;
}
